import React, { Component } from 'react'
import _ from 'lodash';
import axios from '../../../axios';
import { NavLink } from 'react-router-dom';
import { Switch, Route } from 'react-router-dom';
import MUIDataTable from "mui-datatables";

import { Message, Search, Segment, Grid, Label, Icon, Container, Header, Image, Input, Button, Divider } from 'semantic-ui-react'

import Ports from './Ports';

export default class Hosts extends Component {
    state = {
        error: false
    }

    workspaceName = this.props.match.params.workspaceName;

    componentDidMount() {
        axios.get('/workspace/' + this.props.match.params.workspaceName)
            .then(response => {
                this.setState({ domains: response.data.Subdomains });
                console.log(this.state);

            })
            .catch(error => {
                this.setState({ error: true });
            });
    }


    render() {
        if (this.state.error) {
            return (
                <Container fluid>
                    <Header textAlign='center' as='h1' color='red'>No output from the server !</Header>
                </Container>
            )
        }
        
        const columns = [
            {
                name: <Header as='h4'>Domain</Header>,
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Header as='h4'>{value}
                            <a target='_blank' href={'//' + value}>
                                &nbsp;<Icon color='blue' link name='external alternate' />
                            </a>
                            </Header>
                        );
                    }

                }
            },
            {
                name: <Header as='h4'>IP Address</Header>,
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Label color='black' as='h5'>{value}</Label>
                        );
                    }

                }
            },
            {
                name: <Header as='h4'>Ports</Header>,
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Ports content={value} />
                        )

                    }

                }
            },
            {
                name: <Header as='h4'>Technology</Header>,
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Label color='grey' as='h5'>{"Coming soon"}</Label>
                        )
                    }

                }
            }
        
        ]

        let data = []

        _.map(this.state.domains, function (item, index) {
            return (
                data.push([
                    item.Domain,
                    item.IP,
                    item.Ports,
                    item.Technology,
                ])
            )
        })


        const options = {
            filterType: "dropdown",
            rowsPerPage: 25,
            rowsPerPageOptions: [25, 50, 100],
        };

        return (
            <Container fluid>
                <MUIDataTable
                    title={<Header>Host Details</Header>}
                    data={data}
                    columns={columns}
                    options={options}
                />
            </Container>
        )
    }
}
