import React, { Component } from 'react'
import _ from 'lodash';
import axios from '../../axios';

import { Table, Segment, Message, Icon, Container, Header, Input, Divider, Button } from 'semantic-ui-react'

import Config from '../../components/Config/Config';

export default class Configurations extends Component {
    state = {
    }

    handleSubmit = () => {
        console.log(this.state);
    }



    componentDidMount() {
        axios.get('/config')
            .then(response => {
                let configutaions = {};
                _.forEach(response.data, function(value, name) {
                    configutaions[name] = value;
                })
                this.setState({ ...configutaions });
                console.log(this.state);
            })
            .catch(error => {
                this.setState({ error: true });
            });
    }

    render() {

        const handleChange = (e) => {
            this.setState({ [e.target.name]: e.target.value });
        }


        return (
            <Container style={{ marginTop: '7em' }}>
                <Header as='h2' color='violet' textAlign='center'>Options Configurations</Header>
                
                <Button fluid animated='fade' color='green' onClick={this.handleSubmit}>
                    <Button.Content visible>Apply</Button.Content>
                    <Button.Content hidden>
                        <Icon name='check' />
                    </Button.Content>
                </Button>

                <Table celled fixed>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell><Header as='h2' textAlign='center'>Name</Header></Table.HeaderCell>
                            <Table.HeaderCell><Header as='h2' textAlign='center'>Value</Header></Table.HeaderCell>
                            
                        </Table.Row>
                    </Table.Header>

                    <Table.Body>
                        {_.map(this.state, function (value, name) {
                            return (
                            <Table.Row key={name}>
                                <Table.Cell >
                                    <Header as='h3'>{name}</Header>
                                </Table.Cell>
                                <Table.Cell>
                                    <Header as='h4'>
                                            <Input fluid value={value} name={name} onChange={handleChange} />
                                    </Header>
                                </Table.Cell>
                            </Table.Row>
                            )
                        })}

                    </Table.Body>
                </Table> 

                <Button fluid animated='fade' color='green' onClick={this.handleSubmit}>
                    <Button.Content visible>Apply</Button.Content>
                    <Button.Content hidden>
                        <Icon name='check' />
                    </Button.Content>
                </Button>
                <Divider horizontal>-</Divider>



            </Container>
        )
    }
}
