import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { Grid, Message, Button, Header, Image, Divider } from 'semantic-ui-react';
import _ from 'lodash';
import axios from '../../axios';




const ModuleOutput = (props) => (
    
    <Grid celled>
        {_.map(props.content, function(value, index){
            return (
                <Grid.Row>
                        <Grid.Column width={2}>
                            <Header>{index}</Header>
                        </Grid.Column>
                        <Grid.Column width={12}>
                                <Message floating>
                                {value.split('\n').map(function (item, key) {
                                    return (
                                        <span key={key}>
                                            {item}
                                            <br />
                                        </span>
                                    )
                                })}

                                </Message>
                        </Grid.Column>
                    </Grid.Row>
            )
        })}
    </Grid>

);

export default ModuleOutput;