import React from "react";
import ReactDOM from "react-dom";
import MUIDataTable from "mui-datatables";
import _ from 'lodash';
import axios from '../../../axios';
import { Table, Search, Message, Grid, Label, Icon, Container, Header, Image, Input, Button, Divider } from 'semantic-ui-react';

export default class Testing extends React.Component {
    state = {}

    workspaceName = this.props.match.params.workspaceName;

    componentDidMount() {
        axios.get('/logs/' + this.workspaceName)
            .then(response => {
                this.setState({ ...response.data });
                console.log(this.state);

            })
            .catch(error => {
                this.setState({ error: true });
            });
    }

    render() {
        

        const columns = [
            {
                name: "Command",
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            // <Message>{value}</Message>
                            <Message as='h5' floating>{value}</Message>
                        );
                    }

                }
            },
            {
                name: "Output Path",
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Message icon>
                                <Message.Content>
                                    {value}
                                </Message.Content>
                                <a target='_blank' href={'http://127.0.0.1:5000/wscdn/' + value}><Icon size='small' name='external alternate' /></a>
                            </Message>
                        );
                    }

                }
            },
            {
                name: "Std Path",
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Message icon>
                                <Message.Content>
                                    {value}
                                </Message.Content>
                                <a target='_blank' href={'http://127.0.0.1:5000/wscdn/' + value}><Icon size='small' name='external alternate' /></a>
                            </Message>
                        );
                    }

                }
            },
            {
                name: "Status",
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Label color='teal'>{value}</Label>
                        );
                    }

                }
            },
            {
                name: "Modules",
                options: {
                    filter: true,
                    customBodyRender: (value, tableMeta, updateValue) => {
                        return (
                            <Label color='black'>{value}</Label>
                        );
                    }

                }
            },


            ];

        let data = []

        _.map(this.state, function (module, key) {
            return (
                _.map(module, function (value, index) {
                    data.push([
                    value.cmd,
                    value.output_path,
                    value.std_path,
                    value.status,
                    key])
                })
            )
        })


        const options = {
            filterType: "dropdown",
            
            rowsPerPage: "20",
        };

        return (
            <Container fluid>
                

                <MUIDataTable
                    title={<Header as='h2'>Command Logs</Header>}
                    data={data}
                    columns={columns}
                    options={options}
                />
            </Container>
        );
    }
}

