import React from "react";
import ReactDOM from "react-dom";
import MUIDataTable from "mui-datatables";
import _ from 'lodash';
import axios from '../../axios';
import { Table, Search, Message, Grid, Label, Icon, Container, Header, Image, Input, Button, Divider } from 'semantic-ui-react';

export default class Testing extends React.Component {
    state = {}

    componentDidMount() {
        // axios.get('/logs' + '/' + this.workspaceName)
        axios.get('/logs' + '/' + 'zalo.me')
            .then(response => {
                this.setState({ ...response.data });
                console.log(this.state);

            })
            .catch(error => {
                this.setState({ error: true });
            });
    }

    render() {
        const columns = ["Command", "Output Path", "Std Path", "Status", "Module"];

        let data = []

        // const item = 
        // pushing command to data
        _.map(this.state, function(module, key){
            return (
                _.map(module, function (value, index) {
                    data.push([value.cmd, value.output_path, value.std_path, value.status, key])
                    // return (<Header>{value.cmd}</Header>)

                })
            )
        })
        

        const options = {
            filterType: "dropdown",
            responsive: "scroll"
        };

        return (
            <Container fluid style={{ marginTop: '7em' }}>
            <MUIDataTable
                title={"Command Logs"}
                data={data}
                columns={columns}
                options={options}
            />
            </Container>
        );
    }
}

