import React, { Component } from 'react'
import _ from 'lodash';
import axios from '../../axios';

import { Table, Segment, Message, Icon, Container, Header, Input, Divider, Button } from 'semantic-ui-react'

import Routine from '../../components/Config/Routine';

export default class Configurations extends Component {
    state = {
    }

    moduleName = this.props.match.params.moduleName;


    handleSubmit = () => {
        console.log(this.state);
    }

    componentDidMount() {
        axios.get('/routines?module=' + this.moduleName)
            .then(response => {
                
                this.setState({ commands: response.data.routines });
                console.log(this.state);
            })
            .catch(error => {
                this.setState({ error: true });
            });
    }

    render() {

        return (
            <Container style={{ marginTop: '7em' }}>
                <Header as='h2' color='violet' textAlign='center'>{this.moduleName} Module Configurations</Header>

                <Button fluid animated='fade' color='green' onClick={this.handleSubmit}>
                    <Button.Content visible>Apply</Button.Content>
                    <Button.Content hidden>
                        <Icon name='check' />
                    </Button.Content>
                </Button>

                <Table celled fixed>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell><Header as='h2' textAlign='center'>Name</Header></Table.HeaderCell>
                            <Table.HeaderCell><Header as='h2' textAlign='center'>Value</Header></Table.HeaderCell>

                        </Table.Row>
                    </Table.Header>

                   
                        {_.map(this.state.commands, function (item, index) {
                            return (
                                <Table.Body>
                                    {_.map(item, function(value, name){
                                        return (
                                            <Routine index={index} name={name} value={value} />
                                        )
                                    })}
                                    <Table.Row>
                                        <Table.Cell>
                                            <Divider horizontal>
                                                <Header as='h2'>
                                                    -------
                                                </Header>
                                            </Divider>
                                        </Table.Cell>
                                        <Table.Cell>
                                            <Divider horizontal>
                                                <Header as='h2'>
                                                    -------
                                                </Header>
                                            </Divider>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            )
                        })}

                </Table>

                <Button fluid animated='fade' color='green' onClick={this.handleSubmit}>
                    <Button.Content visible>Apply</Button.Content>
                    <Button.Content hidden>
                        <Icon name='check' />
                    </Button.Content>
                </Button>
                <Divider horizontal>-</Divider>



            </Container>
        )
    }
}
