import React, { Component } from 'react'
import { Table, Icon, Button, Header, Input } from 'semantic-ui-react'
import _ from 'lodash';




const Routine = (props) => (
    <Table.Row key={props.index}>
        <Table.Cell >
            <Header as='h3'>{props.name}</Header>
        </Table.Cell>
        <Table.Cell>
            <Header as='h4'>
                <Input fluid value={props.value} name={props.name} />
            </Header>
        </Table.Cell>
    </Table.Row>
);

export default Routine;