import React, { Component } from 'react'
import { Table, Icon, Button, Header, Input } from 'semantic-ui-react'
import _ from 'lodash';
import axios from '../../axios';
import { NavLink } from 'react-router-dom';



const HtmlModule = (props) => (

    <Table.Row >
        <Table.Cell><Header as='h3'>{props.name}</Header></Table.Cell>
        <Table.Cell>
            {_.map(props.content, function(item, key){
                return(
                    <a target='_blank' href={'file:///' + item.output_path}>
                        &nbsp;<Icon color='blue' link name='external alternate' />
                    </a>
                )

            })}

        </Table.Cell>
    </Table.Row>

);

export default HtmlModule;