import React, { Component } from 'react'
import _ from 'lodash';
import axios from '../../axios';

import { Table, Segment, Message, Icon, Container, Header, Image, Card, Button } from 'semantic-ui-react'
import Workspace from '../../components/Workspace/Workspace';


export default class Workspaces extends Component {
    state = {
        workspaces: []
    }

    componentDidMount() {
        axios.get('/workspace')
            .then(response => {
                this.setState({ workspaces: response.data.workspaces });
                console.log(this.state);

            })
            .catch(error => {
                // console.log(error);
                this.setState({ error: true });
            });
    }


    render() {

        return (
            <Container style={{ marginTop: '7em' }}>
                
                <Table basic='very' celled>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell><Header as='h2'>Workspaces</Header></Table.HeaderCell>
                            <Table.HeaderCell><Header as='h2'>Action</Header></Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>

                    <Table.Body>

                        {
                            this.state.workspaces.map(function (value, index) {
                                return <Workspace key={index} value={value} index={index} />
                            })
                        }

                    </Table.Body>
                </Table>

            </Container>
        )
    }
}
