import React, { Component } from 'react'
import { Label, Icon, Button, Header, Input } from 'semantic-ui-react'
import _ from 'lodash';



function Ports (props) {

    if (props.content.length > 0 && props.content !== null) {
        console.log(props.content);
        return(
            _.map(props.content, function (value, index) {
                return (
                    <Label key={index} color='green' as='h5'>{value}</Label>
                )
            })
        )
    }
    else {
        return (<Label color='green' as='h5'>{"N/A"}</Label>)
    }
    
}

export default Ports;
