import React from "react";
import ReactDOM from "react-dom";
import MUIDataTable from "mui-datatables";
import _ from 'lodash';
import axios from '../../../axios';
import { Table, Search, Message, Grid, Label, Icon, Container, Header, Image, Input, Button, Divider } from 'semantic-ui-react';

import ModuleOutput from '../../../components/Workspace/ModuleOutput';

export default class Testing extends React.Component {
    state = {}

    workspaceName = this.props.match.params.workspaceName;
    moduleName = this.props.match.params.moduleName;

    componentDidMount() {
        axios.get('/module/' + this.workspaceName + '?module=' + this.moduleName)
            .then(response => {
                this.setState({ data: response.data[this.moduleName] });
                console.log(this.state);

            })
            .catch(error => {
                this.setState({ error: true });
            });
    }

    render() {
        console.log(this.moduleName);
        return (
            <Container fluid>
                {_.map(this.state.data, function(value, index){
                    return (
                        <div>
                        <ModuleOutput index={index} content={value} />
                            <Divider horizontal>
                                <Header as='h2'>
                                    -------
                                </Header>
                            </Divider>
                        </div>
                    )
                })}

            </Container>
        );
    }
}

