import React, { Component } from 'react'
import _ from 'lodash';
import { Divider, Table, Container, Button, Header, Image,Icon, Grid, Segment } from 'semantic-ui-react';
import { NavLink } from 'react-router-dom';
import axios from '../../../axios';

import Module from '../../../components/Workspace/Module';
import HtmlModule from '../../../components/Workspace/HtmlModule';


export default class Modules extends Component {
    state = { 
    }

    handleClick = () => console.log('Button received click with mouse')

    workspaceName = this.props.match.params.workspaceName;
    
    componentDidMount() {
        axios.get('/module/' + this.workspaceName)
            .then(response => {
                this.setState({ modules: response.data });
                console.log(this.state);

            })
            .catch(error => {
                this.setState({ error: true });
            });
    }


    render() {
        //contain HTML report
        const special_module = ['Screenshot']
        let html_module = null;

        if (this.state.modules) {
            _.map(this.state.modules, function(value, key){
                _.map(special_module, function(item, index){
                    
                    if (key.indexOf(item)) {
                        console.log(value)
                        html_module = <HtmlModule name={item} content={value} />
                    }
                })
                
            })
        }
       
        

        return (
            <Container fluid>
                <Header as='h1' color='green' textAlign='center'>Special Module</Header>
                    <Table basic='very' celled>
                        <Table.Header>
                            <Table.Row>
                                <Table.HeaderCell><Header as='h2'>Modules</Header></Table.HeaderCell>
                                <Table.HeaderCell><Header as='h2'>View</Header></Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>

                        <Table.Body>
                            {html_module}
                        </Table.Body>
                    </Table>

                <Divider horizontal>
                    <Header as='h2'>
                        -------
                    </Header>
                </Divider>

                <Header as='h1' color='blue' textAlign='center'>Normal Module</Header>
                    <Table basic='very' celled>
                        <Table.Header>
                            <Table.Row>
                                <Table.HeaderCell><Header as='h2'>Modules</Header></Table.HeaderCell>
                                <Table.HeaderCell><Header as='h2'>Action</Header></Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>

                        <Table.Body>
                            <Module ws={this.workspaceName} modules={this.state.modules} />
                        </Table.Body>
                    </Table>

            </Container>
        )


    }
}