import React, { Component } from 'react'
import _ from 'lodash';
import axios from '../../axios';

import { Table, Segment, Message, Icon, Container, Header, Image, Card, Button } from 'semantic-ui-react'
import Workspace from '../../components/Workspace/Workspace';
import { NavLink } from 'react-router-dom';

export default class Routines extends Component {
    state = {
    }

    componentDidMount() {
        axios.get('/routines')
            .then(response => {
                this.setState({ routines: response.data.routines });
                console.log(this.state);

            })
            .catch(error => {
                // console.log(error);
                this.setState({ error: true });
            });
    }


    render() {



        return (
            <Container style={{ marginTop: '7em' }}>

                <Table basic='very' celled>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell><Header as='h2'>Modules</Header></Table.HeaderCell>
                            <Table.HeaderCell><Header as='h2'>Action</Header></Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>

                    <Table.Body>

                        {_.map(this.state.routines, function(value, key){

                            return (
                                <Table.Row>
                                    <Table.Cell>
                                        <Header as='h4' image>
                                            
                                            <Image src='https://image.flaticon.com/icons/svg/1541/1541684.svg' rounded size='mini' />
                                            <Header.Content>
                                                <Header as='h2' textAlign='center'>{key}</Header>
                                            </Header.Content>
                                        </Header>
                                    </Table.Cell>

                                    <Table.Cell textAlign='left'>
                                        <NavLink to={'/routine/' + key} exact>
                                        <Button animated='fade' color='purple'>
                                            <Button.Content visible>Details</Button.Content>
                                            <Button.Content hidden>
                                                <Icon name='eye' />
                                            </Button.Content>
                                        </Button>
                                        </NavLink>
                                    </Table.Cell>
                                </Table.Row>
                            )
                        })}

                        

                    </Table.Body>
                </Table>

            </Container>
        )
    }
}
