import React, { Component } from 'react';
import logo from './logo.svg';
import 'semantic-ui-css/semantic.min.css'
import './App.css';
import { BrowserRouter } from 'react-router-dom';
import { Switch, Route } from 'react-router-dom';
import { Container, Header } from 'semantic-ui-react';

import MenuBar from './components/Headers/MenuBar';
import About from './components/About';
import Todo from './components/Todo';

import Workspaces from './containers/Workspaces/Workspaces';
import Details from './containers/Details/Details';
import Configurations from './containers/Configurations/Configurations';
import Routines from './containers/Configurations/Routines';
import RoutineDetails from './containers/Configurations/RoutineDetails';
import ModuleDetails from './containers/Details/Modules/ModuleDetails';
import Testing from './containers/Testing/Testing';
import Testing2 from './containers/Testing/Testing2';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <MenuBar />

        <Switch>
          <Route path='/' exact component={About} />
          <Route path='/workspaces' exact component={Workspaces} />
          <Route path='/configurations/options' exact component={Configurations} />
          <Route path='/configurations/routines' exact component={Routines} />
          <Route path='/testing' exact component={Testing} />
          <Route path='/testing2' exact component={Testing2} />
          <Route path='/todo' exact component={Todo} />

          <Route path='/workspace/:workspaceName/:part' exact component={Details} />
          <Route path='/workspace/:workspaceName' exact component={Details} />

          {/* <Route path='/workspace/:workspaceName/module/:moduleName' exact component={ModuleDetails} /> */}

          <Route path='/routine/:moduleName' exact component={RoutineDetails} />

        </Switch>

      </BrowserRouter>
    );
  }
}

export default App;
