import React, { Component } from 'react'
import { Table, Icon, Button, Header, Input } from 'semantic-ui-react'
import _ from 'lodash';
import axios from '../../axios';
import { NavLink } from 'react-router-dom';



const Module = (props) => (
    _.map(props.modules, function(item, index){
        return (
        <Table.Row >
                <Table.Cell><Header as='h3'>{index}</Header></Table.Cell>
                <Table.Cell>
                    <NavLink to={'/workspace/' + props.ws + '/' + index} exact>
                        <Button animated='fade' color='violet'>
                            <Button.Content visible>Details</Button.Content>
                            <Button.Content hidden>
                                <Icon name='eye' />
                            </Button.Content>
                        </Button>
                    </NavLink>
                </Table.Cell>
        </Table.Row>
        )
    })

);

export default Module;