import React, { Component } from 'react'
import _ from 'lodash';
import axios from '../../axios';
import { NavLink } from 'react-router-dom';
import { Switch, Route } from 'react-router-dom';

import { Table, Segment, Message, Icon, Container, Header, Image, Input, Button, Divider } from 'semantic-ui-react'

export default class Vulnerables extends Component {
    state = {
    }

    workspaceName = this.props.match.params.workspaceName;


    componentDidMount() {
        axios.get('/module/' + this.workspaceName)
            .then(response => {
                this.setState({ reports: response.data.reports });
                console.log(this.state);

            })
            .catch(error => {
                this.setState({ error: true });
            });
    }


    render() {


        return (
            <Container>
                <Header as='h1' color='black' textAlign='center'>Modules contain reports</Header>

                <Table basic='very' celled>
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell><Header as='h2'>Modules</Header></Table.HeaderCell>
                            <Table.HeaderCell><Header as='h2'>Action</Header></Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>

                    <Table.Body>

                        {_.map(this.state.reports, function(value, key){
                            if (value !== 'N/A') {
                                return (
                                    <Table.Row >
                                        <Table.Cell>
                                            <Header as='h4' image>
                                                <Image src='https://image.flaticon.com/icons/svg/1541/1541684.svg' rounded size='mini' />
                                                <Header.Content>
                                                    <Header as='h2' textAlign='center'>{key}</Header>
                                                </Header.Content>
                                            </Header>
                                        </Table.Cell>
                                        <Table.Cell>
                                            <Button role='a' target='_blank' href={'http://127.0.0.1:5000/wscdn/' + value} animated='fade' color='green'>
                                                <Button.Content visible>Details</Button.Content>
                                                <Button.Content hidden>
                                                    <Icon name='eye' />
                                                </Button.Content>
                                            </Button>
                                        </Table.Cell>
                                    </Table.Row>
                                )
                            }
                            
                        })}



                    </Table.Body>
                </Table>

            </Container>
        )
    }
}
